import request from '@/utils/request';

//
export function get_template_config(data) {
  return request({
    method: 'POST',
    url: `/ms_jk/get_template_config`,
    data
  })
}


//
export function upload_file(data) {
  return request({
    method: 'POST',
    url: `/ms_jk/upload_file`,
    data
  })
}

///
export function upload_files(data) {
  return request({
    method: 'POST',
    url: `/ms_jk/upload_files`,
    data
  })
}


//  
export function get_ms_jk_bubble(data) {
  return request({
    method: 'POST',
    url: `/ms_jk/bubble`,
    data
  })
}



//
export function get_sample_data() {
  return request({
    method: 'POST',
    url: `/ms_jk/sample`
  })
}

//
export function get_raw_data(data) {
  return request({
    method: 'POST',
    url: `/ms_jk/read_file`,
    data
  })
}

//
export function get_download_file(data) {
  return request({
    method: 'POST',
    url: `/ms_jk/download`,
    responseType: "blob",
    data
  })
}

//
export function parameter_submission_platform(data) {
  return request({
    method: 'POST',
    url: `/ms_jk/submit`,
    data
  })
}